import React, { useContext} from "react";
import { Link } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import { CloseIcon } from "./AppIcons";

function Content() {
    const { books } = useContext(VersesContext);

    const capitalizeBookShort = (bookShort) => {
        if (Number(bookShort.charAt(0)) < 4) {
            return bookShort.charAt(0) +
                bookShort.charAt(1).toUpperCase() +
                bookShort.slice(2).toLowerCase()
        } else {
            return bookShort.charAt(0).toUpperCase() + bookShort.slice(1).toLowerCase()
        }
    }

    return (
        <div>
            <div className="close-page"><CloseIcon /></div>
            <div className="toc">
                <div className="toc-card" id="starizavjet">
                    <div className="toc-card-header">
                        STARI ZAVJET
                    </div>
                    <ul>
                        {
                        books.filter((book) => (
                            book.testament === "STARI ZAVJET"
                        ))
                        .map(book => (
                            <li key={book.index}>
                                <Link to={`/book/${book.eng_short}`}>
                                    {book.name} ({capitalizeBookShort(book.hrv_short)})
                                </Link>
                            </li>
                            ))
                        }
                    </ul>
                </div>
                <div className="toc-card" id="novizavjet">
                    <div className="toc-card-header">
                        NOVI ZAVJET
                    </div>
                <ul>
                    {
                    books.filter((book) => (
                        book.testament === "NOVI ZAVJET"
                    ))
                    .map(book => (
                        <li key={book.index}>
                            <Link to={`/book/${book.eng_short}`}>
                              {book.name} ({capitalizeBookShort(book.hrv_short)})
                            </Link>
                        </li>
                        ))
                    }
                </ul>
                </div>
            </div>
        </div>
    )
}

export default Content;